<template>
	<div id="content">
    <div class="title">
      <h3>{{infoDetail.title}}</h3>
    </div>
    <div class="context">
      <div v-html="infoDetail.content"></div>
    </div>
    <div class="dianzan">
      <div class="dianzan-title">
        <span class="title-row">
         —————
       </span>
       <span class="title-text">
        以上方案是否解决了您的问题？
      </span>
      <span class="title-row">
       —————
     </span>
   </div>
   <div class="dianzan-content">
     <div>
      <img class="img-responsive" src="../assets/images/yes.png">
      <span>有用</span>
    </div>
    <div>
      <img class="img-responsive" src="../assets/images/no.png">
      <span>没用</span>
    </div>
  </div>

</div>
<div class="help">
  <span>没有找到想问的问题？</span>
  <a href="tel:95399">
    <img class="img-responsive service"  src="../assets/images/helpService.png">
    <span>问问在线客服</span> 
  </a>
  <a @click="gotoService">
    <img class="img-responsive phone" src="../assets/images/helpPhone.png">
    <span>拨打客服电话</span> 
  </a>
</div>
</div>
</template>

<script>
  import config from "../config.js"
  import util from "../utils/commonUtils"
  export default {
    name: 'Content',
    data () {
      return {
        infoDetail:{
          title:""
        }
      }
    },
    created:function(){  
      let titleObject={
        back:true,
        title:"查看解决方案"
      };
      this.$emit("routerChange",titleObject);
      let info =this.$route.params;
      if(util.isEmptyObject(info)){
        this.$router.push({name:"helpMain"});
      }else{
        this.infoDetail=JSON.parse(info.info);
      }
    },
    methods:{
      getInfo:function(index){
        let utilUrl = config.getInfo;
        let url = utilUrl+"/"+index;
        let that = this;
        this.$http.get(url).then(function(response){
          let data=response.body;
          if(data.resCode==0){
            that.infoDetail=data.result;
          }
        });
      },
      gotoService:function(){
        var url ="http://www17.53kf.com/m.php?cid=70867336&arg=9007838&style=1";
        window.location.href = url + "?rnd=" + new Date().getTime();
      }
    }
  }
</script>

<style>
#content {
  min-height: 100%;
  overflow: hidden;
  background: #fff;
}
#content .title {
  padding: 12px 18px;
  margin: 0;
  font-size: 18px;
  color: #000000;
  border-bottom:  0.5px solid #DCDCDC;
}
#content .context{
  margin: 12px 18px;
}
#content img{
  display: block;
  max-width: 100%;
  height: auto;
}
#content .dianzan{
  margin-top: 30px;
  margin-bottom: 90px;
}


#content .dianzan-title{
  font-size: 14px;
  color: #8D8D8D;
  text-align: center;
}
#content .dianzan-title .title-row{
  color: #DCDCDC;
}
#content .dianzan-title .title-text{
  margin: 0 10px;
}
#content .dianzan-content{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #8D8D8D;
  margin: 20px 60px;

}
#content .dianzan-content >div{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#content .dianzan-content img{
  width: 35px;
  margin-bottom: 6px;
}
#content .help{
  position: fixed;
  z-index: 2;
  background: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  font-size: 11px;
  padding: 17px 10px 17px 10px;
  text-align: center;
  border-top: 0.5px solid #dcdcdc;
}
#content .help a{
  display: flex;
}
#content .help .phone{
  width:12px;
  height:15px;
  margin-right: 5px;
  margin-left: 10px;
}
#content .help .service{
  width:17px;
  height:16px;
  margin-right: 5px;
  margin-left: 10px;
}

</style>


