<template>
  <div class="loadMore">
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" >
      <ul>
        <li v-for="(item ,index) in list">
          <div>
            <span>{{item}}</span>
          </div>
        </li>
      </ul>
    </mt-loadmore>
  </div>
</template>

<script>
import { Indicator, Loadmore } from 'mint-ui';
export default {
  name: 'loadMore',
  data () {
    return {
      allLoaded:false,
      list:[1,2,3,4,5,6,7,8,9]
    }
  },
  methods:{
    loadBottom:function(){
      console.log("loadBottom");
    }
  }
}
</script>
<style scoped>
.loadMore{
  background: #ffffff;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: block;
  margin: 0 10px;
  padding:30px 0;
  border-bottom: 1px solid #f1f1f1;
  text-align:center;
}
a {
  color: #42b983;
}
</style>



