<template>
	<div id="main">
    <div class="sortMenu clearfix">
      <mt-navbar class="sortMenu-ul" v-model="selected">
        <mt-tab-item v-bind:id="index" v-bind:key="index" @click.native="getInfos(index,0)" v-for="(category,index) in categoryList">
          {{category.title}}
        </mt-tab-item>
      </mt-navbar>
    </div>
    <div class="no-result" v-if="noResult">
      <span>暂时没有相应类型的问题</span>
    </div>
    <mt-loadmore  :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :autoFill="false" ref="loadmore" >
      <ul class="questions">
        <li v-bind:key="index" @click="gotoDetail(index)"  v-for="(info,index) in infoList">
          <div>
            <span>{{info.title}}</span>
          </div>
          <span><i class="right"></i></span>
        </li>
      </ul>
    </mt-loadmore>
    <div>
      <div class="help">
        <span>没有找到想问的问题？</span>
        <a href="tel:95399">
          <img class="img-responsive service"  src="../assets/images/helpService.png">
          <span>问问在线客服</span> 
        </a>
        <a @click="gotoService">
          <img class="img-responsive phone" src="../assets/images/helpPhone.png">
          <span>拨打客服电话</span> 
        </a>
      </div>
    </div>
  </div>
</template>

<script>
  import config from "../config.js"
  import { Indicator, Loadmore } from 'mint-ui';
  export default {
    name: 'Main',
    data () {
      return {
        search:null,
        selected:0,
        categoryList:null,
        infoList:null,
        categoryIndex:0,
        allLoaded:false,
        pageNumber:0,
        noResult:false,
        pageSize:12,
        infoList:[]
      }
    },
    created:function(){  
      let titleObject={
        back:true,
        title:"帮助中心",
        search:true
      };
      this.$emit("routerChange",titleObject);
      let categoryIndex =this.$route.params.index; 
      if(categoryIndex!=null&&categoryIndex!=undefined){
        this.selected= categoryIndex;
      }
      this.initCategorys();

    },
    methods:{
      initCategorys:function(){
        console.log("initCategorys");
        let url = config.getCategorys;
        let that = this;
        Indicator.open();
        this.$http.get(url).then(function(response){
          Indicator.close();
          let data=response.body;
          if(data.resCode==0){
            that.categoryList=data.result;
            that.getInfos(that.selected);
          }
        });
      },
      gotoSearch:function(){
        this.$router.push({name:"search"});
      },
      getInfos:function(index,pageNumber){
        Indicator.open();
        let utilUrl = config.getInfosByCategoryId;
        let category = this.categoryList[index];
        //重置参数
        this.infoList=[];
        this.selected=index;
        this.pageNumber=0;
        this.allLoaded = false;
        this.noResult=false;
        let url = utilUrl+"?"+"id="+category.id+"&pageNumber="+this.pageNumber+"&pageSize="+this.pageSize;
        let that = this;
        this.$http.get(url).then(function(response){
          Indicator.close();
          let data=response.body;
          if(data.resCode==0){
            console.log(data.result.length);
            that.infoList=data.result;
            if(that.infoList.length>=data.pagination.totalItems){
              that.allLoaded = true;
            }else{
              that.allLoaded = false;
            }
            if(data.result.length==0){
              that.noResult=true;
            }else{
              that.noResult=false;
            }
          }
        });
      },
      moreGetInfos:function(index,pageNumber){
        Indicator.open();
        let utilUrl = config.getInfosByCategoryId;
        let category = this.categoryList[index];
        this.selected=index;
        let url = utilUrl+"?"+"id="+category.id+"&pageNumber="+this.pageNumber+"&pageSize="+this.pageSize;
        let that = this;
        this.$http.get(url).then(function(response){
          Indicator.close();
          let data=response.body;
          if(data.resCode==0){
            for(let i =0; i< data.result.length;i++){
              console.log(data.result[i]);
              that.infoList.push(data.result[i]);
            }
            if(that.infoList.length>=data.pagination.totalItems){
              that.allLoaded = true;
            }else{
              that.allLoaded = false;
            }
          }
          that.$refs.loadmore.onBottomLoaded();
        });
      },
      gotoDetail:function(index){
        console.log(index);
        let info =this.infoList[index];
        console.log(info);
        this.$router.push({name:"content",params:{info:JSON.stringify(info)}});
      },
      imageLoad:function($event){
        if($event.target){
          $event.target.parentNode.parentNode.firstChild.style.height=$event.target.height+"px";
        }
      },
      gotoService:function(){
        var url ="http://www17.53kf.com/m.php?cid=70867336&arg=9007838&style=1";
        window.location.href = url + "?rnd=" + new Date().getTime();
      },
      loadTop:function(){
        console.log("loadTop");
        this.$refs.loadmore.onTopLoaded();
      },
      loadBottom:function(){
        console.log("loadBottom");
        this.pageNumber=this.pageNumber+1;
        this.moreGetInfos(this.selected,this.pageNumber);
      }
    }
  }
</script>

<style scope>
#main{
  background: #fff;
  min-height:100%;
}
#main .questions{
  font-size: 14px;
}
#main .no-result{
  font-size: 14px;
  margin: 15px 10px;
  text-align: center;
}

#main li {
  display: block;
  margin: 0 0 0 16px;
  padding: 13px 0;
  border-bottom: 0.5px solid #f1f1f1;
}
#main .mintui.mintui-search{
  font-size: 20px;
}
#main .nav{
  display: flex;
  flex-flow: nowrap;
}
#main .mint-cell{
  margin-left: 20px;
  border-bottom: 0.5px solid #dcdcdc;
}
#main .mint-cell-wrapper {
  padding-left: 0;
  padding-right: 30px;
}
#main li {
  position: relative;
}
#main li >div{
  padding-right: 35px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

#main li i:after {
  border: solid 2px #c8c8cd;
  border-bottom-width: 0;
  border-left-width: 0;
  content: " ";
  top: 51%;
  right: 20px;
  position: absolute;
  width: 10px;
  height: 10px;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}
#main .title{
  margin-top: 10px;
  margin-bottom: 10px;
}
#main .help-nav{
  margin-top: 20px;
  margin-bottom: 20px;
}

#main .title .title-lead-wrap{
  padding-top: 5px;
  padding-bottom: 5px;
}
#main .title .title-lead{
  color: #000000;
  font-size: 1.6rem;
}
#main .allSortWrap.col-xs-12{
  padding-left: 0;
  padding-right: 0;
}
.ul-container{
  margin-bottom: 50px;
}
.searchbar-inner{
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  background-color: #F5F5F5;
  border-radius: 15px;
  padding: 5px 10px;
}
.searchbar-inner .mintui-search{
  font-size: 1.4rem;
  color: #d9d9d9;
}
.searchbar-inner .mintui-search-text{
  width: 80%;
  font-size: 1.2rem;
  color: #515151;
  padding-top: 2px;
  text-align: center;
}
.allSort{

}

.sortMenu{
  width: 100%; 
  background-color:#fff; 
  overflow-x: scroll; 
  -webkit-overflow-x: scroll;
  border-bottom: 0.5px solid #dcdcdc;
}
.sortMenu::-webkit-scrollbar{ 
  width: 0; 
  height: 0;   
  background-color: #fff;  
}
.sortMenu-ul { 
  min-width:300px; 
  display: flex;
  justify-content: flex-start;
  text-align: center;
  font-size: 13px;
  word-break: keep-all;
  padding-top: 13px;
  padding-bottom: 12px;
  color:#000000;
}
.sortMenu .cell{ 
  display: inline-block; 
  font-size: 1.4rem;
  margin: 0px 0.6rem;
  height: 22px;
  line-height:22px;
}
.sortMenu .cell.special a{
  color: #ff474c;
}
.sortMenu .cell.special:before {
  content: '';
  height: 2px;
  width: 100%;
  background: #ff474c;
  position: absolute;
  bottom: 0px;
}
.sortMenu .all{
  right: 0;
  top: 0;
  height: 30px;
  width: 30px;
  position: absolute;
  background: #fff;
  z-index: 10;
  display: flex;
  justify-content:center;
  align-items:center;
}
.sortMenu .all:before{
  content: '';
  height: 25px;
  width: 1px;
  position: absolute;
  box-shadow: 1px 0px 1px #e0e0e0;
  left: 0px;
}
#main a:hover, a:focus{
  text-decoration: none;
}
#main .allSort, a{
  color: #000;
}
#main  a.is-selected{
  color: red;
  border: 0.5px solid;
  border-radius: 100px;
  padding: 8px 15px;
  margin-bottom: 0;
}
#main .mint-tab-item{
  padding: 8px 15px;
}
#main .mint-navbar > a:first-child{
  margin-left: 10px;
}
#main .mint-navbar > a:last-child{
  margin-right: 10px;
}
#main .mint-cell-wrapper{
  font-size:14px;
  color:#000000;
}
#main .mint-cell-wrapper .mint-cell-title{
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
#main .mint-navbar .mint-tab-item {
  margin-right: 5px;
}

#main .mint-tab-item-label{
  padding-top: 1px;
  padding-bottom: 1px;
}
#main .help{
  position: fixed;
  z-index: 2;
  background: #fff;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  font-size: 11px;
  padding: 17px 10px 17px 10px;
  text-align: center;
  border-top: 0.5px solid #dcdcdc;
}
#main .help a{
  display: flex;
}
#main .help .phone{
  width:12px;
  height:15px;
  margin-right: 5px;
  margin-left: 10px;
}
#main .help .service{
  width:17px;
  height:16px;
  margin-right: 5px;
  margin-left: 10px;
}
</style>


